import React, { Component } from 'react';
import { Row, Form } from 'antd';
import { BtnExport } from 'components/common/button';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullName',
      key: 'fullName',
    },
    {
      title: 'Chuyên ngành',
      dataIndex: 'studyFieldLabel',
      key: 'studyFieldLabel',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'TBCHT TK',
      dataIndex: 'totalCourse',
      key: 'totalCourse',
    },
    {
      title: 'TBCTN',
      dataIndex: 'totalSemester',
      key: 'totalSemester',
    },
    {
      title: 'Xếp hạng',
      dataIndex: 'graduateClassificationLabel',
      key: 'graduateClassificationLabel',
    },
    {
      title: 'Kỳ 1',
      dataIndex: 'gradeOfSemesterNo1',
      key: 'gradeOfSemesterNo1',
    },
    {
      title: 'Kỳ 2',
      dataIndex: 'gradeOfSemesterNo2',
      key: 'gradeOfSemesterNo2',
    },
    {
      title: 'Kỳ 3',
      dataIndex: 'gradeOfSemesterNo3',
      key: 'gradeOfSemesterNo3',
    },
    {
      title: 'Kỳ 4',
      dataIndex: 'gradeOfSemesterNo4',
      key: 'gradeOfSemesterNo4',
    },
    {
      title: 'Kỳ 5',
      dataIndex: 'gradeOfSemesterNo5',
      key: 'gradeOfSemesterNo5',
    },
    {
      title: 'Ghi chú',
      dataIndex: 'reason',
      key: 'reason',
    },
  ];

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      createUpdateListStudentCheckGraduate,
      createUpdateListStudentCheckNoGraduate,
      exportPDF,
      exportExcel,
      disableJoinBtn,
      disableExport
    } = this.props;
    return (
      <div>
        <Row>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnExport title="Đủ điều kiện" onClick={createUpdateListStudentCheckGraduate} disabled={disableJoinBtn} />
            <BtnExport title="Chưa đủ điều kiện" onClick={createUpdateListStudentCheckNoGraduate} disabled={disableJoinBtn} />
            <BtnExport title="Xuất Excel" onClick={exportExcel} disabled={disableExport} />
            <BtnExport title="Xuất PDF" onClick={exportPDF} disabled={disableExport} />
          </div>
        </Row>
        <TableForm
          reload={reload}
          data={data}
          handleSelectedRows={handleSelectedRows}
          rowKey={rowKey}
          columns={this.columns}
          form={form}
          ref={this.refTable}
        />
      </div>
    );
  }
}

export default TableQualifiedDoThesis;



