import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const createUpdateListStudentCheckGraduate = (data) =>
  request({
    prefix,
    url: `/StudentToGraduate/CreateUpdateListStudentCheckGraduate`,
    data,
    method: 'POST',
  });

export const createUpdateListStudentCheckNoGraduate = (data) =>
  request({
    prefix,
    url: '/StudentToGraduate​/CreateUpdateListStudentCheckNoGraduate',
    data,
    method: 'POST',
  });

export const exportExcelStudentGraduateSummary = ({
  fromYear,
  toYear,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentToGraduate.excel`,
    url: '/StudentToGraduate​/ExportExcelStudentGraduateSummary',
    method: 'POST',
    data: {
      fromYear,
      toYear,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });

export const exportPdfStudentGraduateSummary = ({
  fromYear,
  toYear,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `StudentToGraduate.pdf`,
    url: `/StudentToGraduate/ExportPdfStudentGraduateSummary`,
    method: 'POST',
    data: {
      fromYear,
      toYear,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
  });



